.partners-section {
  background-color: #ffffff;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: center;
  align-items: center;
  gap: 20px;
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
}

.partner-logo {
  max-width: 400px;
  max-height: 200px;
  object-fit: contain;
  border-radius: 8px;
}

.contact-us-section {
  background-color: #2d8bba;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo-section {
  flex: 1;
  text-align: center;
  padding: 1% 5%;
}

.dlf-logo {
  max-width: 200px;
  max-height: 200px;
  object-fit: contain;
}

.contact-message .contact-button {
  background-color: transparent;
  color: white;
  border: 2px solid white;
  padding: 12px 24px;
  font-size: 1.1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s ease, background-color 0.2s ease, color 0.2s ease;
}

.contact-message .contact-button:hover {
  transform: scale(1.05);
  color: #2d8bba;
  background-color: white;
}

.contact-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 1% 10%;
}

.contact-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  /* Reduce spacing between containers */
  flex-wrap: wrap;
  /* Allow wrapping for better responsiveness */
}

.contact-message,
.contact-info-box {
  margin: 0;
  padding: 10px;
  color: white;
  font-size: 1.5rem;
}

.contact-message {
  text-align: center;
  font-family: 'Montserrat';
}

.contact-info-box {
  color: #ffffff;
  font-family: 'Crimson pro';
  padding: 40px;
  border-radius: 8px;
  flex: 1;
}

.contact-info-box p {
  margin: 10px 0;
  font-size: 1.5rem;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 300px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.popup-content h2 {
  margin-top: 0;
}

.popup-content form div {
  margin-bottom: 10px;
}

.popup-content label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.popup-content input,
.popup-content textarea {
  width: 90%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.popup-content button {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.popup-content button:hover {
  background-color: #0056b3;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    padding: 2% 0;
  }

  .contact-content {
    flex-direction: column;
  }

  .contact-message,
  .contact-info-box {
    padding: 20px;
  }

  .partners-section {
    flex-wrap: wrap;
  }

  .partner-logo {
    max-width: 80px;
    max-height: 80px;
  }

  .dlf-logo {
    max-width: 120px;
    max-height: 120px;
  }

  .contact-message h2 {
    font-size: 1.5rem;
  }

  .contact-info-box p {
    font-size: 1rem;
  }

  .contact-message .contact-button {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
}